import React from "react";
import {
  TBottomBt,
  TeacherClassForm,
  TeacherClassInfo,
  TeacherFormTop,
  TeacherFormWrap,
  TeacherIdForm,
  TeacherIdInfo,
  TeacherIdItem,
  TeacherImg,
  TeacherImgForm,
  TeacherMemo,
  TeacherMemoForm,
} from "../../../styles/adminstyle/teachercreate";
import { PageTitle } from "../../../styles/basic";
import { Button, Input, Upload, Form, Select } from "antd";
import { UploadOutlined } from "@ant-design/icons";
import TextArea from "antd/es/input/TextArea";
import { GreenBtn, PinkBtn } from "../../../styles/ui/buttons";

const TeacherCreate = () => {
  return (
    <>
      <TeacherFormTop>
        <PageTitle> </PageTitle>
      </TeacherFormTop>
      <Form>
        <TeacherFormWrap>
          {/*  */}
          <TeacherIdInfo>
            <p> </p>
            <TeacherIdForm>
              <TeacherIdItem>
                <Form.Item
                  name="teacherUid"
                  style={{
                    width: "33%",
                  }}
                  rules={[
                    {
                      required: true,
                      message: " .",
                    },
                  ]}
                >
                  <Input placeholder=" " />
                </Form.Item>
                <Form.Item
                  name="teacherUpw"
                  style={{
                    width: "33%",
                  }}
                  rules={[
                    {
                      required: true,
                      message: " .",
                    },
                  ]}
                >
                  <Input placeholder=" " />
                </Form.Item>
              </TeacherIdItem>
            </TeacherIdForm>
          </TeacherIdInfo>
          {/*  */}
          <TeacherIdInfo>
            <p> </p>
            <TeacherIdForm>
              <TeacherIdItem>
                <Form.Item
                  name="teacherNm"
                  style={{
                    width: "33%",
                  }}
                  rules={[
                    {
                      required: true,
                      message: " .",
                    },
                  ]}
                >
                  <Input placeholder=" " />
                </Form.Item>
                <Form.Item
                  name="tcEmail"
                  style={{
                    width: "33%",
                  }}
                  rules={[
                    {
                      required: true,
                      message: "  .",
                    },
                  ]}
                >
                  <Input placeholder=" " />
                </Form.Item>
              </TeacherIdItem>
            </TeacherIdForm>
          </TeacherIdInfo>
          {/*  */}
          <TeacherClassInfo>
            <p> </p>
            <TeacherClassForm>
              <Form.Item
                name="iclass"
                style={{
                  width: "33%",
                }}
                rules={[
                  {
                    required: true,
                    message: " .",
                  },
                ]}
              >
                <Select
                  labelInValue
                  defaultValue={{
                    value: "",
                    label: (
                      <span style={{ color: " rgba(0, 0, 0, 0.25) " }}>
                          
                      </span>
                    ),
                  }}
                >
                  <Select.Option value="1"></Select.Option>
                  <Select.Option value="2"></Select.Option>
                  <Select.Option value="3"></Select.Option>
                </Select>
              </Form.Item>
            </TeacherClassForm>
          </TeacherClassInfo>
          {/*   */}
          <TeacherImg>
            <p> </p>
            <TeacherImgForm>
              <Form.Item
                rules={[
                  {
                    required: true,
                    message: " .",
                  },
                ]}
              >
                <Upload required>
                  <Button icon={<UploadOutlined />}> </Button>
                </Upload>
              </Form.Item>
            </TeacherImgForm>
          </TeacherImg>

          {/*   */}
          <TeacherMemo>
            <p> </p>
            <TeacherMemoForm>
              <Form.Item name="teacherIntroduce">
                <TextArea placeholder=" " />
              </Form.Item>
            </TeacherMemoForm>
          </TeacherMemo>
        </TeacherFormWrap>
        <TBottomBt>
          <GreenBtn></GreenBtn>
          <PinkBtn></PinkBtn>
        </TBottomBt>
      </Form>
    </>
  );
};

export default TeacherCreate;
